#card::-webkit-scrollbar-track{background-color:transparent}#card::-webkit-scrollbar{width:8px}#card::-webkit-scrollbar-thumb{background-color:rgba(0,0,0,.55);border-radius:5px}#card .title h1{font-size:32px;font-weight:400;font-family:"Open Sans","helvetica neue",helvetica,Arial,sans-serif;letter-spacing:2px;padding:60px}#card .title h1::after{width:210px;margin:0 auto;height:1px;background-color:#2a2d34;content:"";display:block}#card{overflow:auto}#card .body{font-size:20px;text-align:justify;margin:0;padding:0}@media (min-width:992px){#card .body{padding:0 20px;flex-direction:row}}#card .description{margin:0;padding:0}#card .contact-method{padding:0;flex-direction:column;margin:0}@media (min-width:992px){#card .contact-method{padding:5px 0;flex-direction:row}#card .contact-method span{padding-left:10px}}#card .contact-method img,#card .language img{width:28px}@media (min-width:992px){#card .contact{padding:15px}}#card .contact{padding:30px 0 0}#card .languages{padding:30px 0 60px}#card .language{text-align:center}#card .language .icon{padding:0 10px}#card .language .col{padding:0}#card .language .score{border:1px solid rgba(42,45,52,.45);border-radius:8px;color:#fff;font-weight:700}#card .language .write{background-color:#31969c}#card .language .speak{background-color:#2a688c}#card .language.portuguese .write{width:33.333333333333336%;margin-left:16.666666666666664%}#card .language.portuguese .speak{width:33.333333333333336%}#card .language.english .write{width:16.666666666666668%;margin-left:33.33333333333333%}#card .language.english .speak{width:16.666666666666668%}#card #profile-pic{padding:0 0 60px}@media (min-width:992px){#card #profile-pic{padding-bottom:0;position:absolute;right:0;top:50%;transform:translateY(-50%)}}#card #profile-pic img{border-radius:50%;margin:0 auto}#card .footer{background-color:#2a2d34}@media (min-width:992px) and (min-height:603px){#card .footer{position:absolute;width:100%;bottom:0}}#card .footer button{background-color:transparent